
import { Vue, Options, Prop } from "vue-property-decorator";

@Options({ name: "SimpleLabeledSelect" })
export default class SimpleLabeledSelect extends Vue {
  @Prop({
    type: Boolean,
    required: false,
    default: false,
  })
  readonly isDisabled: boolean;

  @Prop({
    type: String,
    required: false,
    default: "select",
  })
  readonly name: string;

  @Prop({
    type: Boolean,
    required: false,
    default: false,
  })
  readonly inputIsRequired: boolean;

  @Prop({
    required: false,
    default: "",
  })
  readonly modelValue: string | number;

  @Prop({
    type: String,
    required: false,
    default: "--",
  })
  readonly initialValue: string;

  @Prop({
    type: Object,
    required: false,
    default: undefined,
  })
  readonly options: Object;

  private selectOptions: Object = {};

  created() {
    this.selectOptions = this.options;
    //@ts-ignore
    this.emitter.on(`updateSelectValues${this.name}`, this.updateSelectValues);
  }

  updateSelectValues(options: object) {
    this.selectOptions = options;
  }

  emitValue(event) {
    this.$emit("update:modelValue", event.target.value);
  }
}
